import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Message } from "primeng/primeng";
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ActivatedRoute } from '@angular/router';
import { CusPoolHttpService } from 'app/pages/tzb/custom/http/custom-pool/custom-pool.http.service';
import { ConfirmationService } from 'primeng/components/common/confirmationservice';
import { CustomCenterHttpService } from 'app/pages/tzb/customCenter/http/custom-center.http.service';
import { AppproResultBean, SubApproveMapBean, BussinessMap } from 'app/pages/tzb/custom/credit-ccm/wait-apply/bean/wait-apply.bean';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { CusViewHttpService } from 'app/pages/tzb/custom/http/custom-view/custom-view.http.service';
import * as _ from 'lodash';

@Component({
    selector: 'pool-list-single-allocate',
    templateUrl: './pool-list-single-allocate.component.html',
    styleUrls: ['./pool-list-single-allocate.component.scss'],
    providers: [ConfirmationService, CustomCenterHttpService, CustomeHttpService, CusViewHttpService]
})
export class PoolListSingleAllocateComponent implements OnInit {
    @Output() outValue = new EventEmitter();
    @Input('in-value') inValue: any;
    appproResultBean: AppproResultBean = new AppproResultBean();
    subApproveMapBean: SubApproveMapBean = new SubApproveMapBean();
    bussinessMap: BussinessMap = new BussinessMap();
    advancedSearchDisplay: boolean = false;
    batchFileDisplay: boolean = false;
    custType: string = '';
    advancedSearchInvalue: any;
    batchFileInvalue: any;
    msgs: Message[];
    msgs2: Message[];
    beanExam: any;
    tableList: any = [];
    totalNum: number = 0;
    dataList: any = {};
    dataSource: any = [];
    marketTopic: any = [];
    instancyDegree: any = [];
    custClassifyType: any = [];
    tabIndex: string = '0';
    //分配人员
    personDis: boolean;
    ownerUser: string;
    ownerUserName: string;
    orgId: any;
    userId: any;
    juege: boolean = false;//默认是可以点的
    taskId: string;
    tellerId: string;
    tellerName: string;
    flag: string;
    isHistory: boolean = false;
    code: any;
    ownerInValue: any;
    resultDisplay: boolean = false;
    contactAddr: string;
    contactAddrName: string = '';
    provinceName: string;
    cityName: string;
    zoneName: string;
    streetName: string;
    principalLinkmanName: string;
    principalLinkmanOrgName: string;

    constructor(
        public commfunc: Commfunc,
        public routeInfo: ActivatedRoute,
        public cusPoolHttpService: CusPoolHttpService,
        public confirmationService: ConfirmationService,
        public httpService: CustomeHttpService,
        public httpCusViewService: CusViewHttpService,
    ) {
        this.codeValues();
        this.dataSource = this.code['PoolDataSource'];
        this.marketTopic = this.code['PoolMarketTopic'];
        this.instancyDegree = this.code['PoolinstancyDegree'];
        this.custClassifyType = this.code['PoolCustClassifyType'];
        this.beanExam = JSON.parse(this.routeInfo.snapshot.params.dataMap)
        this.taskId = this.routeInfo.snapshot.params['taskId'];
        // 历史 和 待办 判断-隐藏按钮
        this.flag = this.routeInfo.snapshot.params['markFlag'];
        if (this.flag == '1') {
            this.isHistory = false;
        } else {
            this.isHistory = true;
        }
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    ngOnInit() {
        // 获取任职机构
        let user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (user.extParam && user.extParam.orgId) {
            this.orgId = user.extParam.orgId
        };
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        this.queryBussinessInfo();
    }

    queryBussinessInfo() {
        let dbparam = {
            dedId: this.beanExam.dedId,
            isAuthority: '0', // 是否对登录用户控制权限:0-否 1-是
        }
        let param = {
            inputParams: dbparam
        }
        this.cusPoolHttpService.queryCustPoolPre(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.outputParams) {
                    this.tableList = data.outputParams.resultList == null ? [] : data.outputParams.resultList
                    if (this.tableList.length > 0) {
                        this.dataList = this.tableList[0];
                        if (this.dataList['custId']) {
                            this.selectCustBriefInfo();
                        }
                    }
                    this.totalNum = data.outputParams.totalNum;
                } else {
                    this.tableList = [];
                    this.totalNum = 0;
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });
    }

    // 查看基本信息
    selectCustBriefInfo() {
        let param = {
            custNo: this.dataList['custId']
        }
        this.httpCusViewService.selectCustBriefInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.principalLinkmanName = data.principalLinkmanName;
                this.principalLinkmanOrgName = data.principalLinkmanOrgName;
                if (data.custAttribute == '1' || data.custAttribute == '2') {
                    this.selectPerCustBasicInfo();
                } else {
                    this.selectComCustBasicInfo();
                }
            }
        });
    }

    // 个人-行政区划
    selectPerCustBasicInfo() {
        let param = {
            custNo: this.dataList['custId']
        }
        this.httpCusViewService.selectPerCustBasicInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.contactAddr) {
                    this.contactAddr = data.contactAddr;
                    this.queryContactAddr();
                }
            }
        });
    }

    // 企业-行政区划
    selectComCustBasicInfo() {
        let param = {
            custNo: this.dataList['custId']
        }
        this.httpCusViewService.selectComCustBasicInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.contactAddr) {
                    this.contactAddr = data.contactAddr;
                    this.queryContactAddr();
                }
            }
        });
    }

    queryContactAddr() {
        this.httpCusViewService.queryParentZoneAll({ zoneId: this.contactAddr }).subscribe(data => {
            let province = '', provinceName = '';
            let city = '', cityName = '';
            let zone = '', zoneName = '';
            let street = '', streetName = '';
            data.zoneList.forEach(item => {
                switch (item.parentZoneLevel) {
                    case '1': province = item.parentZoneId, provinceName = item.parentZoneName;
                        break;
                    case '2': city = item.parentZoneId, cityName = item.parentZoneName;
                        break;
                    case '3': zone = item.parentZoneId; zoneName = item.parentZoneName;
                        break;
                    case '4': street = item.parentZoneId; streetName = item.parentZoneName;
                        break;
                    default: break;
                }
            })
            if (province) {
                this.contactAddrName = provinceName;
            };
            if (city) {
                this.contactAddrName = this.contactAddrName + cityName;
            };
            if (zone) {
                this.contactAddrName = this.contactAddrName + zoneName;
            };
            if (street) {
                this.contactAddrName = this.contactAddrName + streetName;
            };
        })
    }

    // 操作
    deal(item) {
        if (item == '1') {// 分配
            this.msgs2 = [];
            this.personDis = true;
        } else if (item == '2') {// 否决
            this.vote();
        }
    }

    // 分配
    allot() {
        // 生成客户受理待办任务
        let arr = [];
        arr.push({ custId: this.dataList.custId, dedId: this.beanExam.dedId, marketingCampaignId: this.dataList.marketingCampaignId, taskId: this.taskId, userId: this.userId })
        let workParam = {
            custInfoList: arr,
            tellerId: this.tellerId,
            tellerName: this.tellerName,
        }
        this.cusPoolHttpService.startCustAssignmentProcess(workParam).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let arrList = data.custList == null ? [] : data.custList;
                this.personDis = false;
                if (arrList.length > 0) {
                    this.resultDisplay = true;
                    this.ownerInValue = arrList;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: '分配成功' });
                    this.doUp();
                }
            } else {
                this.msgs2 = [];
                this.msgs2.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs2 = [];
            this.msgs2.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });
    }

    onHideClose() {
        this.resultDisplay = false;
    }

    // 否决
    vote() {
        this.juege = true;
        this.subApproveMapBean.result = '1';
        this.bussinessMap.approve = '1';
        this.confirmationService.confirm({
            message: '确认否决?',
            accept: () => {
                this.oprate();
            },
            reject: () => {
                this.juege = false;
            }
        });
    }

    // 同意 + 否决
    oprate() {
        this.appproResultBean.bussinessMap = this.bussinessMap;
        this.appproResultBean.userId = this.userId;
        this.appproResultBean.approveMap = this.subApproveMapBean;
        this.appproResultBean.taskId = this.taskId;
        let param = _.clone(this.appproResultBean);
        // 否决操作下inventoryStatus置为‘1’
        if (this.tableList.length > 0 && this.bussinessMap.approve == '1') {
            let arr = JSON.parse(JSON.stringify(this.tableList))
            arr[0]['inventoryStatus'] = '0';
            param.bussinessMap['custInfoList'] = arr;
            param.bussinessMap['inputParams'] = {
                dedId: this.beanExam.dedId,
                inventoryStatus: '0',
                businessStatus: '3',
                operType:'2'
            }
        } else {
            param.bussinessMap['custInfoList'] = this.tableList;
        }
        this.httpService.bpmNextTask(param).subscribe(data => {
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (data.returnCode.code == 'success') {
                this.doUp();
            } else {
                this.juege = false;
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.juege = false;
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }

    // 返回上一页面
    doUp() {
        setTimeout(() => {
            window.history.go(-1);
        }, 500)
    }

    //分配人员
    personDiss(e) {
        this.tellerId = e[0]['tellerId'];
        this.tellerName = e[0]['tellerName'];
        this.confirmationService.confirm({//分配
            message: '确认要分配吗?',
            accept: () => {
                this.allot();
            }
        });
    }

    compileStr(cust) {
        return this.commfunc.compileStr(cust)
    }

    doConfirm() {
        this.outValue.emit();
    }

    doNext() {
        if (!this.custType) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择数据来源！' });
        } else {
            if (this.custType == '1') {
                this.batchFileDisplay = false;
                this.advancedSearchDisplay = true;
            } else if (this.custType == '2') {
                this.advancedSearchDisplay = false;
                this.batchFileDisplay = true;
            }
        }
    }

    close() {
        this.advancedSearchDisplay = false;
        this.batchFileDisplay = false;
    }

    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id);
    }

    fenpei() {

    }

    foujue() {

    }
}