import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { specialSymbol, checkNumber, checkCard } from "app/@uisftech/common/service/validator";
import { CusPoolHttpService } from 'app/pages/tzb/custom/http/custom-pool/custom-pool.http.service';
import { ConfigPropertiesBean } from 'app/pages/tzb/custom/life-cycle/config-properties/bean/config-properties.bean';
import { tableMessage } from "app/@uisftech/common/constant/config";
import { Message } from "primeng/primeng";
import { ConfirmationService } from "primeng/primeng";

@Component({
    selector: 'config-properties',
    templateUrl: './config-properties.component.html',
    styleUrls: ['./config-properties.component.scss'],
    providers: [ConfirmationService]
})
export class ConfigPropertiesComponent implements OnInit {

    bean: ConfigPropertiesBean = new ConfigPropertiesBean();
    tableList: any = [];
    first: number = 0;
    totalNum: number = 0;
    tableMesg: any = tableMessage;
    msgs: Message[] = [];
    upInvalue: any;
    dataType: any = [];
    status: any = [];
    showType: any = [];
    managePermission: any;
    discustom: boolean;
    userform: FormGroup;
    loadingShow: any = { flag: false }; // loading框
    code: any;
    dataList: any = [];
    riskList: any = [];
    idTypeList: any;//证件类型
    statusIdList: any;//状态
    inListTypeList: any;//录入方式
    display: boolean = false;//模态框
    riskdisplay: boolean = false;//模态框
    showModel: any;//模态框判断
    addUpHeader: any;//模态框判断表头
    inValue: any = [];//调整/失效/删除/批量出入库传参
    modifyValue: any;//调整/失效/删除判断
    userId: string;
    key: boolean = false;//现行判断该客户号客户名称存不存在

    constructor(
        public commfunc: Commfunc,
        public fb: FormBuilder,
        public cusPoolHttpService: CusPoolHttpService,
        public confirmationService: ConfirmationService,
    ) {
        this.codeValues() //调用方法，获取全部码值
        this.dataType = this.code['PoolDataType'];
        this.status = this.code['PoolStatus'];
        this.showType = this.code['PoolShowType'];
        // this.dataType = [
        //     { label: '字符串', value: 's' },
        //     { label: '整数', value: 'i' },
        //     { label: '精度数值', value: 'f' },
        //     { label: '分词', value: 'w' },
        //     { label: '日期', value: 'dt' },
        //     { label: '数值报文', value: 'dpi' },
        //     { label: '精度值报文', value: 'dpf' },
        //     { label: '字符串报文', value: 'dps' },
        // ]
        // this.status = [
        //     { label: '失效', value: '0' },
        //     { label: '正常', value: '1' },
        // ]
        // this.showType = [
        //     { label: '文本框', value: '01' },
        //     { label: '下拉框', value: '04' },
        // ]
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        this.managePermission = JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.managePermission
    }

    ngOnInit() {
        this.bean.pageNum = 1;
        this.bean.pageSize = 10;
        this.queryInit();
    }

    // 查询
    queryInit() {
        this.bean.pageNum = 1;
        this.bean.pageSize = 10;
        this.first = 0;
        this.query();
    }

    //查询
    query() {
        let param = {
            inputParams: this.bean
        }
        this.cusPoolHttpService.queryCustPoolConfig(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.outputParams) {
                    this.tableList = data.outputParams.resultList == null ? [] : data.outputParams.resultList
                    this.totalNum = data.outputParams.totalNum;
                } else {
                    this.tableList = [];
                    this.totalNum = 0;
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });
    }

    // 新增
    add() {

        if (this.tableList && this.tableList.length < 30) {
            this.display = true;
            this.showModel = '1';
            this.addUpHeader = '新增自定义属性';
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'info', summary: '提示', detail: "自定义属性不能超过30个！" });
        }
    }

    // 新增回调
    addCall(param) {
        this.display = param;
        this.showModel = '';
        this.queryInit();
    }

    // 调整/失效/出库
    modify(col, i) {
        this.upInvalue = col;
        this.display = true;
        this.showModel = i;
        if (i == '1') {
            this.addUpHeader = '新增自定义属性';
        } else if (i == '3') {
            this.addUpHeader = '编辑';
        } else if (i == '4') {
            this.addUpHeader = '详情';
        }
    }

    // 撤销客户联系策略
    delete(item) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                let param = {
                    inputParams: { indicatorId: item.indicatorId, deleteMode: '0' }
                };
                this.cusPoolHttpService.deleteCustPoolConfig(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: '删除成功' });
                        this.queryInit()
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
                })
            }
        });
    }

    // 调整/失效/出库回调
    updateCall(param) {
        this.display = param;
        this.showModel = '';
        this.queryInit();
    }
    // 详情
    detail(col, i) {
        this.display = true;
        this.inValue = col;
        this.showModel = 4;
        this.modifyValue = i;
        this.addUpHeader = '名单详情';
    }
    // 详情回调
    detailCall(param) {
        this.display = param;
    }
    //批量出入库
    batch(col) {
        this.inValue = col;
        this.display = true;
        this.showModel = 3;
        if (col == '1') {
            this.addUpHeader = '批量入库申请';
        } else if (col == '2') {
            this.addUpHeader = '批量出库申请';
        }
    }

    // 批量出入库回调
    batchCall(param) {
        this.display = param;
    }

    // 重置
    reset() {
        this.bean = new ConfigPropertiesBean();
        this.bean.pageNum = 1;
        this.bean.pageSize = 10;
        this.first = 0;
    }

    //分页
    paginate(event) {
        this.bean.pageSize = event.rows;
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.bean.pageSize;
        this.query();
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }

    close() {
        this.showModel = '';
        this.display = false;
    }
}