
/**
 * @author fanhualing 2019-6-1 全生命周期-维护配置-新增
 */
import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { Message } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { specialSymbol, checkCard, checkNumber, checkEnAndNum } from "app/@uisftech/common/service/validator";
import { ConfigPropertiesAddBean } from "app/pages/tzb/custom/life-cycle/config-properties/config-properties-add/bean/config-properties-add.bean";
import { CusPoolHttpService } from "app/pages/tzb/custom/http/custom-pool/custom-pool.http.service";
import { ConfigPropertiesUpdateBean } from "app/pages/tzb/custom/life-cycle/config-properties/config-properties-update/bean/config-properties-update.bean";
import { tableMessage } from "app/@uisftech/common/constant/config";
import * as _ from 'lodash';

@Component({
    selector: 'config-properties-update',
    templateUrl: './config-properties-update.component.html',
    styleUrls: ['./config-properties-update.component.scss'],
})
export class ConfigPropertiesUpdateComponent implements OnInit {

    @Input('in-value') inValue: any;
    @Output() outValue = new EventEmitter();
    bean: ConfigPropertiesUpdateBean = new ConfigPropertiesUpdateBean();
    dataType: any = [];
    status: any = [];
    showType: any = [];
    msgs: Message[] = [];
    code: any;
    isClick: boolean = false;
    tableMesg: any = tableMessage;
    tableList2: any = [];
    vlaue: string = '';
    oldType: string;

    constructor(
        public commfunc: Commfunc,
        public fb: FormBuilder,
        public cusPoolHttpService: CusPoolHttpService,
    ) {
        this.codeValues()
        this.dataType = this.code['PoolDataType'];
        this.status = this.code['PoolStatus'];
        this.showType = this.code['PoolShowType'];
        // this.dataType = [
        //     { label: '请选择', value: '' },
        //     { label: '字符串', value: 's' },
        //     { label: '整数', value: 'i' },
        //     { label: '精度数值', value: 'f' },
        //     { label: '日期', value: 'dt' },
        // ]
        // this.status = [
        //     { label: '请选择', value: '' },
        //     { label: '失效', value: '0' },
        //     { label: '正常', value: '1' },
        // ]
        // this.showType = [
        //     { label: '请选择', value: '' },
        //     { label: '文本框', value: '01' },
        //     { label: '下拉框', value: '04' },
        // ]
    }
    ngOnInit() {
        if (this.inValue) {
            for (let item in this.inValue) {
                this.bean[item] = this.inValue[item]
            }
        }
        if (this.bean.showType == '04') {
            this.vlaue = '1';
            this.oldType = '04'
            this.queryCustPoolConfig();
        } else {
            this.oldType = '01'
            this.vlaue = '';
        }
    }

    // 客户池配置属性查询
    queryCustPoolConfig() {
        let param = {
            inputParams: {
                indicatorId: this.bean.indicatorId,
            }
        }
        this.cusPoolHttpService.queryIndicatorEnum(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableList2 = data.outputParams.indicatorList == null ? [] : data.outputParams.indicatorList;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });
    }

    // 码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }

    // 保存/提交
    doSave() {
        if (!this.bean.indicatorEnName) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '属性英文名不能为空' }];
            return
        }
        if (!this.bean.indicatorName) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '属性中文名不能为空' }];
            return
        }
        if (!this.bean.dataType) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '属性类型不能为空' }];
            return
        }
        if (!this.bean.dataLength) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '属性长度不能为空' }];
            return
        }
        if (!this.bean.showType) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '界面展示形式不能为空' }];
            return
        }
        if (this.bean.showType == '04' && this.tableList2.length > 0) {
            let aa = false;
            let bb = false;
            for (var i = 0; i < this.tableList2.length; i++) {
                if (!this.tableList2[i].indicatorAttrValue) {
                    aa = true;
                    break
                }
                if (!this.tableList2[i].description) {
                    bb = true
                    break
                }
            }
            if (aa) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '属性值不能为空' }];
                return
            }
            if (bb) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '属性描述不能为空' }];
                return
            }
        }
        this.bean.operType = '2';
        this.isClick = true;
        let param = {
            inputParams: this.bean
        }
        this.cusPoolHttpService.modifyCustPoolConfig(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                // 文本框
                if (this.oldType == '01') {
                    if (this.bean.showType == '04' && this.tableList2.length > 0) {// 现在文本框-不作操作
                        let arr = [];
                        this.tableList2.forEach(element => {
                            arr.push({ indicatorId: this.bean.indicatorId, indicatorAttr: this.bean.indicatorEnName, indicatorAttrValue: element.indicatorAttrValue, description: element.description })
                        });
                        let param = {
                            inputParams: {
                                operType: '1',
                                indicatorList: arr,
                            }
                        }
                        this.cusPoolHttpService.modifyIndicatorEnum(param).subscribe(data => {
                            if (data.returnCode.code == 'success') {
                                this.isClick = false;
                                this.outValue.emit(false)
                            } else {
                                this.isClick = false;
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                            }
                        }, error => {
                            this.isClick = false;
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
                        });
                    } else {
                        this.isClick = false;
                        this.outValue.emit(false)
                    }
                } else {
                    let param = {
                        inputParams: {
                            indicatorId: this.bean.indicatorId,
                        }
                    }
                    this.cusPoolHttpService.deleteIndicatorEnum(param).subscribe(data => {
                        if (data.returnCode.code == 'success') {
                            if (this.bean.showType == '01') {
                                this.isClick = false;
                                this.outValue.emit(false)
                            } else {
                                if (this.tableList2.length > 0) {
                                    let arr = [];
                                    this.tableList2.forEach(element => {
                                        arr.push({ indicatorId: this.bean.indicatorId, indicatorAttr: this.bean.indicatorEnName, indicatorAttrValue: element.indicatorAttrValue, description: element.description })
                                    });
                                    let param = {
                                        inputParams: {
                                            operType: '1',
                                            indicatorList: arr,
                                        }
                                    }
                                    this.cusPoolHttpService.modifyIndicatorEnum(param).subscribe(data => {
                                        if (data.returnCode.code == 'success') {
                                            this.isClick = false;
                                            this.outValue.emit(false)
                                        } else {
                                            this.isClick = false;
                                            this.msgs = [];
                                            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                                        }
                                    }, error => {
                                        this.isClick = false;
                                        this.msgs = [];
                                        this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
                                    });
                                } else {
                                    this.isClick = false;
                                    this.outValue.emit(false)
                                }
                            }
                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                        }
                    }, error => {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
                    });
                }
            } else {
                this.isClick = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.isClick = false;
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });
    }

    // 界面展示形式
    changeShowType(item) {
        if (item == '04') {
            this.vlaue = '1';
            if (this.tableList2.length == 0) {
                this.add();
            }
        } else {
            this.vlaue = '';
        }
    }

    // 新增
    add() {
        let aa = _.clone(this.tableList2);
        aa.push({
            fieldValue: '', describe: '',
        });
        this.tableList2 = aa;
    }

    // 删除
    delete(item, i) {
        if (item.sequenceId) {
            let param = {
                inputParams: {
                    sequenceId: item.sequenceId,
                }
            }
            this.cusPoolHttpService.deleteIndicatorEnum(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.queryCustPoolConfig();
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
            });
        } else {
            this.tableList2.splice(i, 1);
            this.tableList2 = _.clone(this.tableList2);
        }
    }

    // 返回
    return() {
        this.outValue.emit(false)
    }
}