import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { ConfigPropertiesComponent } from './config-properties/config-properties.component';
import { ListAfterEnterThePoolComponent } from './list-after-enter-the-pool/list-after-enter-the-pool.component';
import { PoolCustomerAllocationsComponent } from 'app/pages/tzb/custom/life-cycle/pool-customer-allocations/pool-customer-allocations.component';
import { PoolListSingleAllocateComponent } from 'app/pages/tzb/custom/life-cycle/pool-customer-allocations/pool-list-single-allocate/pool-list-single-allocatecomponent';
import { PoolListSingleWaitDoComponent } from 'app/pages/tzb/custom/life-cycle/pool-customer-allocations/pool-list-single-wait-do/pool-list-single-wait-docomponent';

const routes: Routes = [
    { path: 'config-properties', component: ConfigPropertiesComponent },
    { path: 'life-cycle-query', loadChildren: './life-cycle-query/life-cycle-query.module#LifeCycleQueryModule' },
    { path: 'pool-customer-allocations', component: PoolCustomerAllocationsComponent },
    { path: 'pool-list-sigle-allocate', component: PoolListSingleAllocateComponent },//待办处理界面
    { path: 'pool-list-sigle-wait-do', component: PoolListSingleWaitDoComponent },//待办处理界面
]

export const LifeCycleRouting: ModuleWithProviders = RouterModule.forChild(routes);